.heart {
  content: '\2764';
  color: red;
}
.footer {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 82px;
  @include mq(mobile) {
    flex-wrap: wrap;
  }
  @include mq(tablet) {
    width: 100%;
    height: 100px;
  }
}
.footer__text {
  display: inline-block;
  font-size: 14px;
  line-height: 1.2;
  font-style: normal;
  font-weight: 400;
  text-align: center;

  color: var(--footer-text);
  width: 252px;
  @include mq(tablet) {
    width: 384px;
    font-size: 16px;
    line-height: 1.25;
  }
}

.footer__button {
  width: 160px;
  height: 40px;
  position: relative;
  margin-left: 5px;

  place-items: center;
  overflow: hidden;
  background: var(--main-bg-color);
  color: var(--modal-bg-color);
  border: none;
  transition: transform 350ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  &:hover,
  &:focus {
    color: var(--accent-color);
    transform: scale(1.1);
  }

  @include mq(mobile) {
    margin-top: 10px;
  }
  @include mq(tablet) {
    margin-top: 0px;
  }
}
.footer__button::before {
  content: '';
  position: absolute;
  width: 20px;
  height: 170px;
  left: 60px;
  top: -64;
  background: linear-gradient(var(--btn-bg-color), var(--accent-color));
  animation: animate 5s linear infinite;
}
.footer__button::after {
  content: '';
  position: absolute;
  background: var(--main-bg-color);
  inset: 2px;
  border-radius: 5px;
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.footer__btn-text {
  position: relative;
  z-index: 1;
}
