.close-button {
  width: 18px;
  height: 18px;
  cursor: pointer;
  border: none;
  position: absolute;
  top: 25px;
  right: 25px;
}

.close-button:before, .close-button:after {
  content: "";
  height: 22px;
  width: 3px;
  background-color: var(--btn-bg-color);
  position: absolute;
  top: -4px;
  left: 10px;
}

